.home {
  min-height: 100vh;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 clamp(2rem, 5vw, 4rem);
}

@media (min-width: 1921px) {
  .container {
    max-width: 1920px;
  }
}
/* Hero Section */
.hero {
  position: relative;
  min-height: clamp(600px, 50vh, 800px);
  overflow: hidden;
  padding: 4rem 0;
}

.hero-content {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 clamp(2rem, 5vw, 4rem);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  align-items: center;
  position: relative;
  z-index: 2;
}

@media (min-width: 1921px) {
  .hero-content {
    max-width: 1920px;
  }
}
.hero-text {
  position: relative;
  z-index: 3;
}

.hero-text h1 {
  font-family: "Outfit", sans-serif;
  font-weight: 800;
  font-size: clamp(3rem, 6vw, 6rem);
  line-height: 1.26;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #EAC04A;
  margin: 0 0 3rem 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.cta-button {
  font-family: "Outfit", sans-serif;
  font-weight: 700;
  font-size: clamp(18px, 1.5vw, 24px);
  line-height: 30px;
  text-transform: uppercase;
  color: #000000;
  background: transparent;
  border: 5px solid #000000;
  border-radius: 81px;
  padding: 1.2rem 3rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cta-button:hover {
  background: #000000;
  color: #EAC04A;
}

.hero-image-container {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 400px;
}

.blob {
  position: absolute;
  border-radius: 30% 70% 70% 30%/30% 30% 70% 70%;
  z-index: 1;
}

.blob {
  width: clamp(250px, 35vw, 500px);
  height: clamp(250px, 35vw, 500px);
  background: #EAC04A;
  top: -5%;
  right: -10%;
  transform: rotate(-24.22deg);
  z-index: 0;
}

.hero-image {
  position: relative;
  z-index: 2;
  width: 100%;
  height: auto;
  object-fit: cover;
}

/* Scout Groups Section */
.scout-groups {
  padding: 4rem 0;
}

.groups-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: clamp(1.5rem, 3vw, 2rem);
  max-width: 1400px;
  margin: 0 auto;
}

.group-card {
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease;
}

.group-card h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 700;
  font-size: clamp(18px, 1.5vw, 20px);
  line-height: 25px;
  text-transform: uppercase;
  color: #000000;
  margin: 0;
}

/* Instagram Section */
.instagram-section {
  padding: 4rem 0 6rem;
}

.section-title {
  font-family: "Maname", serif;
  font-weight: 400;
  font-size: clamp(2.5rem, 4vw, 3rem);
  line-height: 1.2;
  color: #000000;
  margin: 0 0 3rem 0;
}

.instagram-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 0;
  max-width: 1400px;
  margin: 0 auto;
}

.instagram-post {
  aspect-ratio: 1;
  background: #D9D9D9;
  border: 5px solid #FFFFFF;
  transition: opacity 0.3s ease;
}

.instagram-post:hover {
  opacity: 0.9;
}

/* Responsive */
@media (max-width: 1024px) {
  .hero-content {
    grid-template-columns: 1fr;
  }
  .hero-text h1 {
    font-size: clamp(2rem, 8vw, 3rem);
  }
  .groups-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
  .instagram-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}
@media (max-width: 640px) {
  .instagram-grid {
    grid-template-columns: 1fr;
  }
  .groups-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
